<template>
	<BaseSelect
		:label="$lang.edit.form.thumbnailSelector"
		:options="options"
		:selected="value"
		:disabled="disabled || !files.length"
		@input="$emit('input', $event)"
		name="Thumbnail"
	/>
</template>

<script>
import BaseSelect from "@/components/base/BaseSelect.vue";

export default {
	components: {
		BaseSelect,
	},
	props: {
		value: {
			type: String,
			default: "",
			required: true,
		},
		disabled: {
			type: Boolean,
			default: false,
		},
		files: {
			type: Array,
			default: () => [],
		},
		resourceId: {
			type: String,
			required: true,
		},
	},
	computed: {
		options() {
			return [
				{ key: "", value: this.$lang.edit.form.thumbnailAutogenerate },
				...this.files.map((elem) => {
					let obj = {};
					const key =
						this.$config.API.contentServerUrl +
						this.$config.API.hostingEntry +
						this.resourceId +
						"/" +
						elem;
					obj.key = key;
					obj.value = elem;
					return obj;
				}),
			];
		},
	},
};
</script>
